body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
}

.contact-page {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    justify-content: space-between;
}

/* Top Section (Logo + Text) */
.contact-top-section {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 40px 20px;
}

.company-logo {
    display: flex;
    align-items: center;
}

.logo {
    width: 50px;
    margin-right: 10px;
}

.company-details {
    text-align: left;
}

.company-details p {
    margin-bottom: 10px;
    font-size: 18px;
}

.get-quote-btn {
    padding: 10px 20px;
    background-color: #333;
    color: white;
    border: none;
    cursor: pointer;
}

.get-quote-btn:hover {
    background-color: #555;
}

/* Bottom Section (Quick Links + Contact Info) */
.contact-bottom-section {
    display: flex;
    justify-content: center;
    /* Center the contact section */
    text-align: center;
    padding: 40px 20px;
    background-color: #f0f0f0;
    margin-top: auto;
}

.contact-info {
    width: 100%;
    /* Full width for smaller screens */
}

.contact-info h3 {
    font-size: 20px;
    margin-bottom: 10px;
}

.contact-info p {
    margin-bottom: 5px;
}

.social-links {
    display: flex;
    justify-content: center;
    gap: 15px;
    margin-top: 10px;
}

.social-icon {
    font-size: 24px;
    color: #333;
}

.social-icon:hover {
    color: #555;
}

/* Footer */
.footer {
    text-align: center;
    padding: 10px;
    background-color: #333;
    color: white;
    width: 100%;
}

/* Responsive styling for screens up to 765px */
@media (max-width: 765px) {
    .contact-top-section {
        flex-direction: column;
        align-items: center;
    }

    .company-logo,
    .company-details {
        text-align: center;
    }

    .footer {
        padding: 20px;
        font-size: 14px;
    }

    .social-links {
        gap: 10px;
    }
}