.navbar {
    background-color: #333;
    padding: 1rem;
}

.navbar-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 1200px;
    margin: 0 auto;
}

.logo img {
    height: 50px;
    width: auto;
}

.company-name {
    color: #fff;
    font-size: 1.5rem;
    font-weight: bold;
    margin-left: 0.75rem;
}

.nav-links {
    display: flex;
    gap: 2rem;
    list-style: none;
    margin: 0;
    padding: 0;
}

.nav-links li {
    display: inline;
}

.nav-links a {
    color: #fff;
    text-decoration: none;
    font-size: 1rem;
    font-weight: bold;
}

.nav-links a:hover {
    color: #ffcc00;
}

/* Responsive adjustments for screens up to 765px */
@media (max-width: 765px) {
    .navbar-container {
        flex-direction: column;
        align-items: center;
        padding: 0.5rem;
        /* Adjustments for mobile view */
    }

    .logo {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        width: 100%;
    }

    .logo img {
        height: 40px;
        /* Adjust logo size for smaller screens */
    }

    .company-name {
        font-size: 1.2rem;
        margin-left: 0.5rem;
    }

    .nav-links {
        flex-direction: column;
        gap: 1rem;
        align-items: center;
        margin-top: 1rem;
        width: 100%;
    }

    .nav-links a {
        font-size: 0.9rem;
    }
}